html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

#root {
  width: 100%;
  height: 100%;
}
.App {
  background-color: #eeeee4;
  width: 100%;
  height: 100%;;
  margin: auto;
  display: flex;
  flex-direction: column;
}

.App .content {
  flex: 1 1;
  top: 0px;
  bottom: 50px;
  position: absolute;
  width: 100%;
  overflow: auto;
  display: flex;
}

.head{
  font-family: 'IBM Plex Sans Thai', sans-serif;
  background-color: #d9b490 ;
  color: white;
  text-align: center;
  font-size: 35pt;
  margin-bottom: 10%;
}


.tabbar {
  font-family: 'IBM Plex Sans Thai', sans-serif;
  height: 50px;
  background-color: #d9b490;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.tabbar .tabbutton {
  display: inline-block;
  text-align: center;
  font-size: 1.5em;
  color: white;
  margin: 0 5px;
  padding: 5px;
  cursor: pointer;
  text-decoration: none;
  
}

.tabbar .tabbutton:hover {
  background-color: #b08051;;
  border-radius: 25px;
}

.tabbar.stick-bottom {
  position: absolute;
  bottom: 0px;
  width: 100%;
}

.tabbar.stick-top {
  position: absolute;
  top: 0px;
  width: 100%;
}

.tabbar.stick-left {
  position: absolute;
  left: 0px;
  height: 100%;
  flex-direction: column;
}

.tabbar.stick-right {
  position: absolute;
  right: 0px;
  height: 100%;
  flex-direction: column;
}
.avatar {
  margin-top: 5px;
  margin-right: 15px;
  margin-left: 15px;
  margin-bottom: 7px;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: solid 4px white;
  overflow: hidden;
}

.avatar.small {
  width: 40px;
  height: 40px;
}
.chatbox {
  flex-direction: column;
  display: flex;
  flex: 1 1;
  background-color: #d9b490;
  overflow: auto;
}

.privatechatbox {
  scroll-behavior: auto;
  flex-direction: column-reverse;
  display: flex;
  flex: 1 1;
  background-color: #d9b490;
  overflow: auto;
}


.chatbox-item{
  transition: top 1s opacity 1s display 1s width 1s height 1s;
}

.chatbox-item.left{
  text-align: left;
}

.chatbox-item.right{
  text-align: right;
}

.chatbox-item .avatar{
  display: inline-block;
}

.bg{
  background-color: #eeeee4;
  border-radius: 25px;
  min-height: 100px;
  padding: 20px;
  align-items: center;
  margin: 5px;
}
.chatbox-item-text{
  font-family: 'IBM Plex Sans Thai', sans-serif;
  font-size: 1em;
  color: black;
 
}

.titlebar {
  font-family: 'IBM Plex Sans Thai', sans-serif;
  font-size: 35pt;
  height: 50px;
  background-color: #b08051;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  color: aliceblue;
}
.userlist {
  position: absolute;
  top: 0px;
  bottom: 0px;
  background-color: #331a12;
  z-index: 100;
  width: 300px;
}

.userlist.closed {
  width: 0px;
  transition: width 2s;
}

.userlist.open {
  width: 300px;
  transition: width 2s;
}

.userlist .close-button {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 14pt;
  background-color: azure;
  border: gray 1px solid;
  border-radius: 50%;
  float: right;
  width: 32px;
  height: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.userlist .close-button:hover {
  background-color: #cee740;
}

@media screen and (min-width: 768px) {
  .userlist {
    flex: 2 1;
    position: relative;
    background-color: #06668c;
    width: unset;
    max-width: 300px;
  }

  .userlist .close-button {
    display: none;
  }
}
.chat {
  position: relative;
  height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  scroll-behavior: auto;

  flex:4 1;
}
.privatechat{
  flex-direction: column;
}

.inputbox {
  background-color: #eeeee4;
  display: flex;
  height: 70px;
}

.inputbox .label1 {
  font-size: 1.5em;
  text-align: right;
}

.inputbox .text {
  margin:5px;
  border-radius: 25px;
  border: none;
  outline: none;
  height: 50px;
  font-size: 1em;
  padding-left: 8px;
  padding-right: 8px;
  max-height: 60px;
  flex: 1 1;
}

.inputbox .button {
  background-color: #b08051;
  border: none;
  border-radius: 25px;
  font-size: 1em;
  color: white;
  min-width: 100px;
  max-height: 60px;
}
.Profile {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background-color: #eeeee4;
  padding: 0;
  margin: 0;
}
.button{
  font-family: 'IBM Plex Sans Thai', sans-serif;
  margin: 10px;
  background-color: #b08051;
  border: none;
  border-radius: 25px;
  font-size: 1em;
  color: white;
  min-width: 100px;
  min-height: 40px;
}
.button:hover {
  background-color: #7cbc00; /* Green */
  color: white;
}
.button:active {
  background-color: White;
}

.text{
  font-family: 'IBM Plex Sans Thai', sans-serif;
  padding: 5px 5px;
  outline: none;
  margin: 10px;
  border: none;
  border-radius: 25px;
  font-size: 1em;
  min-width: 100px;
  min-height: 40px;
}

.profilepic{
  width: 50px;
  height: 50px;
  background-color: White;
  border-radius: 25px;
}

.quote{
  padding: 20px 0;
  text-align: center;
  font-family: 'IBM Plex Sans Thai', sans-serif;
  font-size: 1em;
  height: 20px;
  width: 50%;
  border-radius: 25px;
  background-color: white;
}


.friend-list {
  background-color: #d9b490;
  flex:1 1
}

.friend-list-item {
  font-size: 20pt;
  color: white;
  display: flex;
  align-items: center;
  padding: 0.5rem;
  border-bottom: 5px solid #b08051;
  cursor: pointer;
}

.friendhead{
  font-family: 'IBM Plex Sans Thai', sans-serif;
  background-color: #b08051;
  color: white;
  font-size: 40pt;
  text-align: center;
}

.friendname{
  font-family: 'IBM Plex Sans Thai', sans-serif;
  margin: 15px;
}
